import {throttle} from 'lodash';

// http://alistapart.com/article/expanding-text-areas-made-elegant
function makeExpandingArea(container) {
	const area = container.querySelector('textarea');
	const span = container.querySelector('span');
	const THROTTLE_TIME = 200;

	function onInput(ev) {
		// if prssed key is enter set new height
		// span.textContent = (area.value === '') ? ' ' : area.value;
		span.textContent = area.value;
		// area.offsetHeight;
	}

	const throttledOnInput = throttle(onInput, THROTTLE_TIME);

	if (area.addEventListener) {
		area.addEventListener('input', throttledOnInput, false);
		// area.addEventListener('keypress', (ev) => {
		// 	const key = ev.keyCode || ev.wich;
		// 	if ( key === 13) {
		// 		// to make input see changes on new line
		// 		span.textContent = area.value + '<br/>';
		// 	}
		// });
		onInput();

	} else if (area.attachEvent) {
		// IE8 compatibility
		area.attachEvent('onpropertychange', throttledOnInput);
		onInput();
	}
	// Enable extra CSS
	container.className += ' expanding-area_active';

	// container.querySelector('pre').appendChild(document.createElement('br'));
}

export default makeExpandingArea;



